import { mapState } from 'vuex'

let timeStop = null

export default {
  data () {
    return {
      scrollIndex: 0,
      oHeight: 0,
      transitionTime: 0.6
    }
  },
  created () {
    this.computedUlTopFn()
  },
  onShow () {
    this.computedUlTopFn()
  },
  onReady () {
    this.computedWrapperHeightFn()
  },
  computed: {
    ...mapState([
      'allGrabList'
    ])
  },
  methods: {
    computedUlTopFn () {
      clearInterval(timeStop)
      timeStop = setInterval(() => {
        this.scrollIndex++
        if (this.scrollIndex >= this.allGrabList.length) {
          this.scrollIndex = 0
          this.transitionTime = 0
        } else {
          this.transitionTime = 0.6
        }
      }, 2500)
    },
    computedWrapperHeightFn () {
      const that = this
      const query = uni.createSelectorQuery().in(this)
      setTimeout(() => {
        query.select('.all-grab-floating-window').boundingClientRect((rect) => {
          if (rect) {
            that.oHeight = rect.height
          } else {
            that.computedWrapperHeightFn()
          }
        }).exec()
      }, 3000)
    },
    // 跳转页面
    onSkipPageFn (url) {
      uni.navigateTo({ url })
    }
  },
  onHide () {
    clearInterval(timeStop)
  }
}
